@mixin flex($x: center, $y: center){
    display: flex; justify-content: $x; align-items: $y;
    @content;
}

@mixin font($a: inherit, $b: inherit, $c: inherit){
    font-size: $a; font-weight: $b; color: $c;
    @content;
}

*{
    margin: 0; padding: 0;
    box-sizing: border-box;
}

body{
    background-color: rgb(250, 255, 170);
}

ul, li{
    list-style-type: none;
}

button, input[type='submit'], li{
    cursor: pointer;
}








header{
    background-color: white;
    text-align: center;
    margin-bottom: 20px;
    border-bottom: 2px solid black;
    &.active{
        display: none;
    }

    a{
        text-decoration: none;
    }

    >a.homeA{
        display: block;
        @include font(1rem, bold, black);
        padding: 5px 0;
    }

    >.subAWrapper{
        background-color: rgb(100, 155, 255);
        color: white;
        padding: 10px 0;
        
        >a{
            @include font(1.2rem, bold, white);
        }
    }
}

article{
    @include flex(flex-start, center){flex-direction: column}
}

article.Home{
    >h1{
        // outline: 1px solid blue;

        @include font(3rem, bold);
        text-align: center;

    }//h1
    
    >p{
        // outline: 1px solid blue;
        
        @include flex();
        border: 1px solid black; border-radius: 50%;
        width: 200px; height: 200px;
        margin: 50px 0;
        background-color: white;
        
        overflow: hidden;
        
        >img{
            width: 90%; height: 90%;
        }
    }//p
    
    >div.btnsWrapper{
        // outline: 1px solid blue;

        @include flex(space-between);
        width: 50%; height: 50px;
        >button{
            border: 0;
            background-color: black;
            @include font(1.2rem, inherit, white);
            width: 49%; height: 100%;
        }
    }//div.btnsWrapper
}//article.Home

//--------------------------------------------------

article.Write{
    // outline: 1px solid blue;
    width: 50%; margin: auto;
    >h2{
        @include font(1rem, bold, black);
        margin-bottom: 10px;
    }//h2

    >form{
        @include flex(flex-start, center){flex-direction: column}
        width: 80%;
        >input{
            border: 1px solid black;
            width: 100%;

            border-radius: 5px;
            padding: 10px;
        }//input

        >input.name{
            margin-bottom: 5px;
        }//input.name

        >input.tel{
            margin-bottom: 10px;
        }//input.tel

        >input.submit{
            background-color: rgb(100, 155, 255);
            @include font(1.2rem, bold, white);
        }//input.submit
    }//form
}//article.Write

//--------------------------------------------------

article.List{
    // outline: 1px solid blue;
    padding: 0 20px;
    >h2{
        @include font(1rem, bold, black);
        margin-bottom: 50px;
    }//h2
    
    >ul.listWrapper{
        // outline: 1px solid blue;
        width: 60%;
        >li{
            @include flex(space-between);
            border: 1px solid black; border-radius: 5px;
            background-color: white;
            padding: 10px;
            margin-bottom: 5px;
            
            >div.nameWrapper{
                @include flex();
                >div.profile{
                    @include flex();
                    border-radius: 50%;
                    background-color: gray;
                    width: 30px; height: 30px;
                    color: white;

                    margin-right: 10px;
                }

                >span.name{
                    @include font(1rem, inherit, black)
                }//span.name
            }//div.profile


            >a.tel{
                @include flex();
                border-radius: 50%;
                background-color: yellowgreen;
                width: 30px; height: 30px;
                padding: 3px;
                
                >img{
                    width: 100%; height: 100%;
                }
            }//a.tel
        }//li
    }//ul.listWrapper
    
}//article.List




